*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  -webkit-font-smoothing: antialiased;
  min-height: 100vh;
  line-height: 1.5;
}

img, picture, video, canvas, svg {
  max-width: 100%;
  display: block;
}

input, button, textarea, select {
  font: inherit;
}

ul[role="list"], ol[role="list"] {
  list-style: none;
}

details summary {
  cursor: pointer;
}

details summary > * {
  display: inline;
}

body {
  background-color: var(--body-background-color);
  color: var(--body-foreground-color);
  font-family: var(--body-font-family, system-ui), serif;
  font-size: var(--body-font-size);
  place-items: center;
  display: grid;
}

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
}

a {
  color: inherit;
}

nav a {
  text-decoration: none;
}

summary {
  list-style: none;
}

button {
  font: inherit;
  cursor: pointer;
  color: currentColor;
  appearance: none;
  background: none;
  border: 0;
}

[hidden] {
  display: none !important;
}

:focus {
  outline: 2px solid #0000;
}

:focus-visible {
  outline-color: var(--outline-color, currentColor);
}

ul:not([role="list"]) {
  padding-left: 1rem;
}

.skip-link:focus {
  z-index: 1000;
  background-color: var(--skip-link-background-color);
  box-shadow: var(--skip-link-box-shadow);
  font-size: var(--skip-link-font-size);
  padding: .5rem 1rem;
  text-decoration: none;
  display: inline-block;
  position: absolute;
  top: 1rem;
  left: 1rem;
}

.main-container[data-page="home"] {
  --padding-x: 0;
  --padding-y: 0;
}

.main-container {
  flex: 1;
  justify-content: center;
  display: flex;
}

:not([data-page="home"]) + .root .main-container {
  --padding-x: 0;
  --padding-y: 0;
  height: 100%;
}

@media (width >= 43.75em) {
  :not([data-page="home"]) + .root .main-container {
    --padding-x: 2rem;
    --padding-y: 2rem;
  }
}

.footer {
  background-color: var(--taskbar-background-color);
  box-shadow: var(--footer-box-shadow);
}

.footer__inner {
  place-content: center flex-start;
  gap: .1875rem;
  padding: .25rem .375rem;
  display: flex;
  position: relative;
}

.root {
  background-color: var(--root-background-color);
  grid-template-rows: 1fr auto;
  width: 100%;
  height: 100%;
  display: grid;
}

@media (width >= 43.75em) {
  .root {
    max-width: min(100vw, var(--root-max-width));
    max-height: min(100vh, var(--root-max-height));
    overflow: auto;
  }
}

.root__desktop {
  overflow: auto;
}

@media (width >= 43.75em) {
  .root__desktop {
    gap: 1rem;
    display: flex;
  }
}

.root__taskbar {
  background-color: var(--taskbar-background-color);
  position: sticky;
  bottom: 0;
  left: 0;
}

.nav__list {
  justify-content: start;
  display: grid;
}

.nav__list[data-parent="header"] {
  --icon-min-size: 4.75rem;
  --gap: .75rem;
  gap: var(--gap);
  color: #fff;
  grid-template-columns: auto auto;
}

[data-parent="header"] .nav__link {
  text-align: center;
  justify-content: center;
  justify-items: center;
  gap: .25rem;
  display: grid;
}

[data-parent="header"] .nav__link[data-draggable] {
  opacity: 0;
  -webkit-user-select: none;
  user-select: none;
  position: absolute;
  inset: 0;
}

[data-parent="header"] .nav__link.on-drag {
  opacity: .6;
}

[data-parent="header"] .nav__link--name {
  background-color: #00000073;
  padding: 0 .25ch;
  line-height: 1;
}

[data-parent="header"] .nav__item {
  -webkit-user-select: none;
  user-select: none;
  position: relative;
}

[data-parent="header"] .nav__item img {
  -webkit-user-select: none;
  user-select: none;
}

.menu__toggle {
  box-shadow: var(--menu-toggle-inactive-box-shadow);
  align-items: center;
  width: fit-content;
  padding: .33rem .375rem;
  display: flex;
}

.menu[open] .menu__toggle {
  box-shadow: var(--menu-toggle-active-box-shadow);
  background-color: #fff5;
}

.menu__content {
  min-width: var(--menu-width);
  max-height: calc(var(--root-max-height, 100vh)  - var(--taskbar-height));
  background-color: var(--menu-background-color);
  box-shadow: var(--menu-box-shadow);
  background-image: var(--menu-gradient);
  background-repeat: no-repeat;
  background-size: 1.5rem;
  padding: .125rem .125rem .125rem 1.5rem;
  font-size: .9rem;
  position: absolute;
  bottom: 1.85rem;
  left: 1px;
  overflow: auto;
}

.menu .nav__list {
  grid-template-columns: 1fr;
  justify-items: stretch;
  gap: 0;
  font-size: .9em;
  display: grid;
}

.menu .nav__link {
  justify-content: flex-start;
  align-items: center;
  gap: .5rem;
  width: 100%;
  height: 2rem;
  padding: .125rem 2.8125rem .125rem .625rem;
  display: flex;
}

.menu .nav__item:hover {
  background-color: var(--menu-item-hover-background-color);
  color: var(--menu-item-hover-foreground-color);
}

.window {
  --padding-x: .75rem;
  --padding-y: .75rem;
  background-color: var(--window-background-color);
  grid-template-rows: auto auto 1fr;
  width: 100%;
  height: 100%;
  display: grid;
  overflow: auto;
}

@media (width >= 43.75em) {
  .window {
    resize: both;
    max-width: min(100%, var(--max-window-width));
    max-height: min(100%, var(--max-window-height));
    min-width: min(100vw, 17rem);
    min-height: min(100vh, 15rem);
  }
}

.window__header {
  background-image: var(--window-header-background-image);
  box-shadow: var(--window-header-box-shadow);
  color: var(--window-header-foreground-color);
  align-items: center;
  gap: 1ch;
  margin: 2px;
  padding: 0 .1rem 0 .5ch;
  display: flex;
}

.window__heading {
  flex: 1;
}

.window__navbar {
  align-items: center;
  margin: 2px .25rem;
  font-size: .75rem;
  line-height: 1.3;
  display: flex;
}

.window__navbar span {
  margin: 0 1px;
  padding: 2px 1ch;
  display: inline-block;
}

.window__navbar span:first-letter {
  text-decoration: underline;
}

.window__navbar span:hover {
  box-shadow: var(--menu-toggle-active-box-shadow);
}

.window__content {
  border-top: 1px solid #929292;
  margin: 5px;
  overflow: auto;
}

.window__content .empty-state {
  padding: 1rem;
}

.bookmark {
  --padding-x: .75rem;
  padding: var(--padding-x) var(--padding-y);
  box-shadow: var(--bookmark-box-shadow);
  gap: .25rem;
  margin: .75rem 0;
  display: grid;
}

.bookmark:hover {
  box-shadow: var(--bookmark-hover-box-shadow);
}

.bookmark__title a {
  text-decoration: none;
}

.bookmark:first-child {
  margin-top: 0;
}

.bookmark:last-child {
  margin-bottom: 0;
}

:root {
  --body-background-color: #222;
  --body-foreground-color: #000;
  --root-background-color: teal;
  --skip-link-background-color: silver;
  --taskbar-background-color: silver;
  --menu-background-color: silver;
  --menu-gradient: linear-gradient(#050481, #1084d0);
  --menu-item-hover-background-color: #050481;
  --menu-item-hover-foreground-color: #fff;
  --window-background-color: silver;
  --window-header-background-image: linear-gradient(to right, #050481, #1084d0);
  --window-header-foreground-color: #fff;
  --body-font-family: Arial, system-ui;
  --body-font-size: .9rem;
  --skip-link-font-size: 1rem;
  --taskbar-height: 1.7rem;
  --menu-width: 12rem;
  --menu-height: 30.3rem;
  --root-max-width: 68.75rem;
  --root-max-height: 37.5rem;
  --max-window-width: 45rem;
  --max-window-height: 30rem;
  --skip-link-box-shadow: 2px 2px #f4f4f4, inset -1px -1px #414141, inset -2px -2px #414141;
  --footer-box-shadow: inset -1px -1px 0 #0c0c0c, inset 1px 1px 0 #bbc3c4, inset -2px -2px 0 #808088, inset 2px 2px 0 #fff;
  --menu-toggle-inactive-box-shadow: inset 2px 2px #f4f4f4, inset -1px -1px #414141, inset -2px -2px #414141;
  --menu-toggle-active-box-shadow: inset -1px -1px 0 #fff, inset 1px 1px 0 #414141, inset -2px -2px 0 #a0abac, inset 2px 2px 0 #808088;
  --menu-box-shadow: inset -1px -1px 0 #0c0c0c, inset 1px 1px 0 #bbc3c4, inset -2px -2px 0 #808088, inset 2px 2px 0 #fff;
  --bookmark-hover-box-shadow: inset -1px 0 #f5f5f5, inset 0 -1px #f5f5f5, inset 1px 0 #808088, inset 0 1px #808088;
  --bookmark-box-shadow: inset -1px -1px 0 #0c0c0c, inset 1px 1px 0 #bbc3c4, inset -2px -2px 0 #808088, inset 2px 2px 0 #fff;
}

:not([data-page="home"]) + .root .header {
  display: none;
}

@media (width >= 43.75em) {
  :not([data-page="home"]) + .root .header {
    display: inline-block;
  }
}

[data-overlap] .icon {
  display: block;
  position: relative;
}

[data-overlap] .icon:after {
  content: "";
  z-index: 5;
  background-image: linear-gradient(45deg, #0000 45%, red 46% 54%, #0000 55%);
  border-radius: 50%;
  width: 1.25em;
  height: 1.25em;
  position: absolute;
  bottom: 0%;
  left: 0%;
  transform: translate(-25%, 25%);
  box-shadow: inset 0 0 0 3px red;
}

.visually-hidden, .skip-link:not(:focus), [data-page="home"] .main:not(:focus) {
  clip: rect(0 0 0 0);
  white-space: nowrap;
  border: 0;
  width: 1px;
  height: auto;
  margin: 0;
  padding: 0;
  position: fixed;
  overflow: hidden;
}

.wrapper {
  padding: var(--padding-x, 1rem) var(--padding-y, 1rem);
}

.flow > * + * {
  margin-top: var(--flow-space, .5rem);
}

.divider {
  color: inherit;
  border: 0;
  border-bottom: 1px solid #929292;
  margin: 1rem 0;
}
